.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 250px;
  width: 250px;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(black, #f09819);
  left: -150px;
  top: -120px;
}
.shape:last-child {
  background: linear-gradient(to right, black, #f09819);
  right: -80px;
  bottom: -120px;
}
.log-form {
  height: 520px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.13);
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
.log-form * {
  font-family: 'Poppins', sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}
.log-form .form-title {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}
.form-label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
.form-input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}
::placeholder {
  color: #e5e5e5;
}
.form-btn {
  margin-top: 50px;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}
.form-btn:hover {
  background-color: #080710;
  color: #ffffff;
}
.social {
  margin-top: 30px;
  display: flex;
}
.social div {
  width: 150px;
  border-radius: 3px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.27);
  color: #eaf0fb;
  text-align: center;
  transition: 0.3s;
}
.social div:hover {
  background-color: rgba(255, 255, 255, 0.58);
}
.social .fb {
  margin-left: 25px;
}
.social i {
  margin-right: 4px;
}
